'use strict';

/**
* Return the current working directory of the process.
*
* @module @stdlib/process-cwd
*
* @example
* var cwd = require( '@stdlib/process-cwd' );
*
* var dir = cwd();
* // returns '/path/to/current/working/directory'
*/

// MODULES //

var cwd = require( './main.js' );


// EXPORTS //

module.exports = cwd;
