'use strict';

// MAIN //

/**
* Returns default presentation options.
*
* @private
* @returns {Object} default options
*/
function defaults() {
	var options = {
		'borderTop': '*',
		'borderBottom': '*',
		'borderLeft': '* ',
		'borderRight': ' *',
		'newline': '\n',
		'whitespace': ' ',
		'width': null,
		'height': null,
		'counter': false,
		'workspace': 'presentation',
		'autoClear': true,
		'loop': false
	};
	return options;
}


// EXPORTS //

module.exports = defaults;
