'use strict';

// MODULES //

var hasOwnProp = require( '@stdlib/assert-has-own-property' );
var ANSI_ESCAPE_CODES = require( './ansi_escape_codes.js' );


// MAIN //

/**
* Returns the ANSI escape code corresponding to a provided escape code name.
*
* @private
* @param {string} name - ANSI escape code name
* @returns {(string|null)} escape code
*/
function escapeCode( name ) {
	if ( hasOwnProp( ANSI_ESCAPE_CODES, name ) ) {
		return ANSI_ESCAPE_CODES[ name ];
	}
	return null;
}


// EXPORTS //

module.exports = escapeCode;
